.content{
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  text-align: center;
  vertical-align: middle;
  margin: 2vh 3vw;
  font-size: 1em;
}
.particles{
  position: fixed;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  z-index: -1;
}
h1{
  font-size: 4em;
  margin: 1rem;
  padding: 1rem;
}
.button{
  position: relative;
  margin: 1rem;
  padding: 0.5rem 1rem;
  border-radius: 0.5rem;
  display: -ms-inline-flexbox;
  display: inline-flex;
  border: 1px solid black;
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
}
.button:hover{
  -webkit-transform: scale(1.1);
      -ms-transform: scale(1.1);
          transform: scale(1.1);
  -webkit-box-shadow: 0 0 15px #ff0010;
          box-shadow: 0 0 15px #ff0010;
}
a{
  text-decoration: none;
  color: black;
}
.contact-detail{
  margin: 1rem;
  font-size: 1em;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
}
.social{
  min-width: 30vw;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  margin: 3rem 7vh;
}
span{
  -webkit-transition: .5s;
  -o-transition: .5s;
  transition: .5s;
  font-size: 20px;
  display: inline;
}
span:hover{
  font-size: 50px;
}


